                                                                                          <p className="text-sm text-gray-800 dark:text-gray-200 m-2">DocTraitant est dite par Angicia, socit SAS au capital de 1000 , dont le sige social est situ  Toulouse, inscrite au RCS de Toulouse sous le 2424242424. Numro de TVA intracommunautaire : 2424242424</p>

