function determineTitle(gender: string) {
    return gender === 'F' ? 'Mme.' : 'M.';
}

function formatHeureMinutes(dateString: string) {
    const date = new Date(dateString);
    const heures = date.getHours();
    const minutes = date.getMinutes();

    const heureMinutes = `${heures}h${minutes < 10 ? '0' : ''}${minutes}`;

    return heureMinutes;
}

function formaterDateAvecJour(dateString: string) {
    const date = new Date(dateString);
    const joursSemaine = ["Dimanche", "Lundi", "Mardi", "Mercredi", "Jeudi", "Vendredi", "Samedi"];
    const mois = ["janvier", "fvrier", "mars", "avril", "mai", "juin", "juillet", "aot", "septembre", "octobre", "novembre", "dcembre"];

    const jourSemaine = joursSemaine[date.getDay()];
    const jour = date.getDate();
    const moisAnnee = mois[date.getMonth()];
    const annee = date.getFullYear();
    const heures = ('0' + date.getHours()).slice(-2);
    const minutes = ('0' + date.getMinutes()).slice(-2);

    return `${jourSemaine} ${jour} ${moisAnnee} ${annee}  ${heures}h${minutes}`;
}

interface EventItemProps {
    data: DocRequestData;
    itemToogle: boolean;
    itemToggleBoolean: (data: DocRequestData) => void;
    modalTrigger: (value: boolean) => void;
}

