import React, { useState, ChangeEvent, forwardRef, useImperativeHandle } from 'react';
import { UserResponse } from '../../types/UserResponse';
import { UserModelData } from '../../types/userApiModel';

interface UserSearchBarProps {
    selectedItem: string[] | undefined;
    onSelectedItemsChange: (item: string) => void;
    onClearItemsChange: () => void;
    placeholder: string;
}
export interface UserSearchBarHandle {
    clearSearch: () => void;
}

const UserSearchBar: React.ForwardRefRenderFunction<UserSearchBarHandle, UserSearchBarProps> =
    ({ selectedItem, onSelectedItemsChange, onClearItemsChange, placeholder }, ref) => {

        useImperativeHandle(ref, () => ({
            clearSearch: () => {
                setIsDropdownOpen(false);
                setInputValue('');
            },
        }));

        const jsonUrl = "https://api.doctraitant.fr/api/user/users-support-assignments/";

        const [searchResults, setSearchResults] = useState<UserModelData[]>([]);

        const [inputValue, setInputValue] = useState('');
        const [isDropdownOpen, setIsDropdownOpen] = useState(false);

        const [isAutocompleteLoad, setIsAutocompleteLoad] = useState(false);

        const handleInputChange = async (event: ChangeEvent<HTMLInputElement>) => {
            const inputValue = event.target.value;
            setInputValue(inputValue);
            setIsDropdownOpen(true);
            setIsAutocompleteLoad(true)
            await searchPostalCodes(inputValue);
        };

        const handleDocumentClick = (event: MouseEvent) => {
            const target = event.target as Node;
            if (!document.getElementById('searchbar-container')?.contains(target)) {
                setIsDropdownOpen(false);
                setInputValue('');
            }
        };

        const searchPostalCodes = async (entry: string) => {
            try {
                const response = await fetch(jsonUrl);
                if (!response.ok) {
                    throw new Error('Failed to fetch data');
                }

                const data: UserResponse = await response.json();

                const filteredResults = data.data.filter((item) =>
                    item.email.startsWith(entry)
                );
                setSearchResults(filteredResults);
                setIsAutocompleteLoad(false)
            } catch (error) {
                setIsAutocompleteLoad(false)
                console.error('Error searching postal codes:', error);
            }
        };

        return (
            <>
                <div className="mt-4 w-full">
                    <div className="relative">
                        <input
                            type="text"
                            id="hs-leading-icon"
                            name="hs-leading-icon"
                            className="py-3 px-4 ps-11 block w-full border border-gray-200 shadow-sm rounded-md text-sm disabled:opacity-50 disabled:pointer-events-none"
                            placeholder={placeholder}
                            value={inputValue}
                            onChange={handleInputChange} />
                        <div className="absolute inset-y-0 start-0 flex items-center pointer-events-none ps-4">
                            {
                                isAutocompleteLoad ? (
                                    <div className="animate-spin inline-block w-5 h-5 border-[3px] border-current border-t-transparent text-gray-400 rounded-full" role="status" aria-label="loading">
                                        <span className="sr-only">Loading...</span>
                                    </div>
                                ) : (
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="flex-shrink-0 h-4 w-4 text-gray-400 w-6 h-6">
                                        <path strokeLinecap="round" strokeLinejoin="round" d="m21 21-5.197-5.197m0 0A7.5 7.5 0 1 0 5.196 5.196a7.5 7.5 0 0 0 10.607 10.607Z" />
                                    </svg>
                                )
                            }
                        </div>
                    </div>
                    {isDropdownOpen && inputValue && searchResults.length > 0 && (
                        <ul className="autocomplete absolute mt-1 w-48 sm:w-64 bg-white rounded-lg border border-gray-300 shadow-md z-[40]">
                            {searchResults.slice(0, 3).map((item, index) => (
                                <li
                                    key={index}
                                    className="autocomplete-item py-2 px-2 cursor-pointer hover:bg-gray-100 transition duration-200"
                                    onClick={() => onSelectedItemsChange(item.email)}
                                >
                                    <p className="text-start flex items-center text-sm gap-x-1 text-[#1E6091] font-normal cursor-pointer">
                                        <svg fill="#1E6091" className="w-4 h-4" version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512.201 512.201"><g id="SVGRepo_bgCarrier" strokeWidth="0"></g><g id="SVGRepo_tracerCarrier" strokeLinecap="round" strokeLinejoin="round"></g><g id="SVGRepo_iconCarrier"> <g> <g> <path d="M415.194,202.761c-29.653,0.427-54.613,23.467-57.493,52.907c-2.773,28.373,15.147,54.293,42.027,62.08 c-0.96,12.8-5.867,27.2-10.453,40c-1.387,3.84-2.667,7.573-3.84,11.2c-8.107,24.533-16.96,48-37.013,65.493 c-43.627,38.4-103.467,58.667-163.947,56c-24.747-1.067-55.147-8.853-67.093-23.573c-4.587-5.76-6.187-12.267-4.907-20.053 c3.52-20.8,19.733-33.707,37.013-47.36c5.44-4.373,11.093-8.853,16.32-13.547c28.373-25.813,40.213-50.453,41.92-87.893 c81.493-7.36,139.093-72,139.093-159.253c0-23.253-3.307-35.84-11.307-57.067c-0.107-0.32-0.32-0.64-0.427-0.96 c-8.107-16.32-32.427-43.84-68.48-39.04c0.213-0.213,0.427-0.32,0.64-0.533c4.907-3.307,6.187-9.92,2.987-14.827 c-3.2-4.693-9.387-6.187-14.293-3.307c-12.8,8.107-18.453,23.573-14.293,39.573c4.907,18.987,17.387,22.187,30.08,22.827h0.533 c5.867,0.107,10.773-4.48,10.88-10.453c0.107-5.867-4.48-10.773-10.453-10.88c-2.133,0-4.267-0.32-6.293-0.747 c30.507-6.08,47.253,22.507,49.387,26.453c6.827,18.027,9.707,28.267,9.707,48.96c0,80.32-56.107,138.667-133.333,138.667 c-73.493,0-133.333-67.947-133.333-151.573c0-18.88,2.88-26.773,9.067-44.16l0.533-1.387c0.213-0.427,16.32-35.947,41.813-37.44 c-5.76,1.387-9.28,7.04-8,12.8c1.28,5.227,6.187,8.747,11.627,8.213c10.987-1.813,23.147-5.333,28.587-24.213 c4.693-16.427-0.533-31.467-13.44-38.4c-5.227-2.773-11.627-0.853-14.4,4.373c-2.773,5.227-0.853,11.627,4.373,14.4 c1.067,0.533,1.92,1.387,2.453,2.453c-46.08-9.173-71.467,46.827-72.853,49.92l-0.747,2.027 c-6.507,17.92-10.453,28.8-10.453,51.413c0,93.227,66.347,169.173,149.013,172.587c-1.6,30.933-11.093,50.027-34.987,71.68 c-4.8,4.373-10.027,8.427-15.253,12.693c-18.667,14.827-39.893,31.68-44.693,60.587c-2.347,13.76,0.96,26.56,9.387,37.013 c22.187,27.413,72.747,31.04,82.667,31.467c3.947,0.213,7.787,0.32,11.733,0.32c61.973,0,122.347-22.08,167.253-61.653 c24-21.12,34.56-48.533,43.2-74.88c1.173-3.413,2.453-7.04,3.733-10.773c4.907-13.973,10.24-29.653,11.52-45.013 c30.507-2.56,53.867-27.947,53.867-58.453C474.82,228.681,448.047,202.227,415.194,202.761z M416.154,298.761 c-20.587,0-37.333-16.747-37.333-37.333c0-20.587,16.747-37.333,37.333-37.333c20.587,0,37.333,16.747,37.333,37.333 C453.487,282.014,436.74,298.761,416.154,298.761z"></path> </g> </g> </g></svg>
                                        <span>{item.firstname} {item.lastname}</span>
                                    </p>
                                </li>
                            ))}
                        </ul>
                    )}
                    <div className="py-5">
                        <div className="relative flex items-center justify-between">
                            <div className="absolute inset-y-0 right-0 flex items-center">
                                <div className="relative flex flex-row text-sm">
                                    <p onClick={() => onClearItemsChange()} className="text-start flex items-center text-xs gap-x-1 pr-1 font-normal cursor-pointer">
                                        <span className="text-[#FC5C65]">Tout supprimer</span>
                                    </p>
                                    <p onClick={() => onSelectedItemsChange(inputValue)} className="text-start flex items-center text-xs gap-x-1 pl-1 font-normal cursor-pointer">
                                        <span className="text-sky-700">Ajouter</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    };

export default forwardRef(UserSearchBar);